import React from 'react';

const InfoDisplay = ({ accountBalance }) => {
    return (
        <div className="infoDisplay">
            <div id="infoHead">
                Current Balance: <br />
                 
                $ {accountBalance}
            </div>
            <span id="warningBox" className="warningMessage">
                
            </span>
        </div>
    )
}

export default InfoDisplay
