import React from 'react';


const Amount = ({ onChange, atmMode, accountBalance, setAccountBalance }) => {
    
    const updateBalance = (event) => {
        event.preventDefault();
        let amount = event.target.numberinput.value;
        if (amount === 0) { setAccountBalance(0) };
        atmMode === 'Deposit' ? setAccountBalance(parseInt(amount) + parseInt(accountBalance)) : setAccountBalance(parseInt(accountBalance) - parseInt(amount));
        document.getElementById('numberinput').value = '';

    };

    const resetBox = (event) => {
        event.preventDefault();
        document.getElementById('numberinput').value = '';
        document.getElementById('submitButton').removeAttribute('disabled');
        document.getElementById('warningBox').innerHTML = '';
    }

    return (
        <span className='amount'>
            <span id="atmModeDisplay">
                    Please select Deposit or Withdrawal.
            </span>

            <form onSubmit={updateBalance}>
                <input id="numberinput" required min="0" type="number" width="200" onChange={onChange} />
                    <br />
                <input type="submit" width="200" value="Submit" id="submitButton" className="submitButton" />
            </form>
            <form>
                <input type="submit" width="200" value="Clear" id="clearButton" className="clearButton" onClick={resetBox} />
            </form>

        </span>
    )
}

export default Amount
