import React from 'react';
import Header from './components/Header';
import NavBar from './components/NavBar';
import InfoDisplay from './components/InfoDisplay';
import Amount from './components/Amount';

function App() {
  const [accountBalance, setAccountBalance] = React.useState(0);
  const [atmMode, setAtmMode] = React.useState("Deposit");

const handleChange = (event) => {
  event.preventDefault();
  console.log(`handleChange ${event.target.value}`);
  console.log(accountBalance);

  let stopButton = document.getElementById('submitButton');
  let warningMessage = document.getElementById('warningBox');

  if (event.target.value > accountBalance && atmMode === 'Withdraw') {
    stopButton.disabled = 'true';
    warningMessage.innerHTML = 'Amount entered greater than account balance.';
  } else {
    warningMessage.innerHTML = '';
    stopButton.removeAttribute('disabled');

  }
  
  if (atmMode === 'Deposit'){
    stopButton.removeAttribute('disabled');
    warningMessage.innerHTML = '';
  };
};

  const setTransaction = (id) => {
    setAtmMode(id);
    document.getElementById('numberinput').value = '';
    document.getElementById('warningBox').innerHTML = '';
    document.getElementById('atmModeDisplay').innerHTML = `Amount to ${id} :`;
    };

  return (
    <div className="grid-container">
      <Header />
      <NavBar setTran={setTransaction}/>
      <InfoDisplay accountBalance={ accountBalance } />
      <Amount onChange={handleChange} atmMode={atmMode} accountBalance={accountBalance} setAccountBalance={setAccountBalance}/>
    </div>
  );
}

export default App;
