import React from 'react';
import { IconContext } from "react-icons";
import { GiPayMoney } from "react-icons/gi";
import { GiReceiveMoney } from "react-icons/gi";


const NavBar = ({setTran}) => {
    return (

    <div className='navBar' >
        <IconContext.Provider value={{ className: "global-class-name" }}>
            <button type='button' className='navButton' id='depButton'  onClick={() => setTran('Deposit')}>
                <GiPayMoney size={70} />
                <br />
                <span className='butText'>   
                Deposit
                </span> 
            </button>
            <button type='button' className='navButton' id='withButton' onClick={() => setTran('Withdraw')}>
                <GiReceiveMoney size={70} />
                <br />
                <span className='butText'>
                Withdraw
                </span>
            </button>
        </IconContext.Provider>
    </div>

    )
}

export default NavBar
